import Vue from "vue";
import db from "../../db";
import VueFire from "vuefire";

Vue.use(VueFire);

export default {

  name: 'edit',

  data(){

    return{

      product: this.$route.params.product,

      token: localStorage.getItem('TOKEN') ,

      content:{
        title: "",
        firmness: "",
        body: ""
      },

      size:{
        size: "",
        price: ""
      }

    }

  },

  firebase() {
    return {

      DB_SIZES: db.ref(this.product + '/sizes'),
      DB_CONTENT: db.ref(this.product + '/content')

    }
  },


  computed:{

    store_sizes(){

      return this.$store.state.sizes;

    }

  },

  mounted(){

    this.$http.get(this.product + '/content.json?auth='+ this.token).then( res => {
      
      return res.json();

    }).then(data => {

      this.content.title = data.title;
      this.content.firmness = data.firmness;
      this.content.body = data.body;
      
    })

  },


  methods:{

    updateStore(){

      // dispatch ACTION to UPDATE the SIZES in the STORE
      this.$store.dispatch('add_new_size');

    },



    onSubmitContent(){

      this.$firebaseRefs.DB_CONTENT.set(this.content);

      //this.updateStore();

    },


    onAddSize(){

      this.$firebaseRefs.DB_SIZES.push(this.size);

      //this.updateStore();

    },


    onDelete(item){

      this.$firebaseRefs.DB_SIZES.child(item['.key']).remove();

      //this.updateStore();

    },

    updateSize: function (item, newSize) {

      this.$firebaseRefs.DB_SIZES.child(item['.key']).child('size').set(newSize);

      //this.updateStore();

    },

    updateSizePrice: function (item, newSizePrice) {

      this.$firebaseRefs.DB_SIZES.child(item['.key']).child('price').set(newSizePrice);

      //this.updateStore();

    },

  }

}


