
import Firebase from 'firebase';


// Initialize Firebase
let config = {
    apiKey: "AIzaSyA40E-WuhXZZ77_ZeDZ2SJ5Tcug3W5CXWI",
    authDomain: "angular2curso.firebaseapp.com",
    databaseURL: "https://angular2curso.firebaseio.com",
    projectId: "angular2curso",
    storageBucket: "angular2curso.appspot.com",
    messagingSenderId: "894835197849"
};

let firebaseApp = Firebase.initializeApp(config);



firebaseApp.auth().onAuthStateChanged( firebaseUser => {

    if(firebaseUser){

        firebaseUser.getIdToken(/* force refresh */true).then(token => {

            if(token){

                console.log('LOGGED IN');

                localStorage.setItem('TOKEN', token);

            }

        });

    }else{

        console.log('LOGGED OUT');

    }

});


export default firebaseApp;


